export function pointAdd(a, b) {
    return {
        x: a.x + b.x,
        y: a.y + b.y,
    };
}
export function rectCenter(rect, relative = false) {
    if (relative) {
        return {
            x: rect.width / 2,
            y: rect.height / 2,
        };
    }
    else {
        return {
            x: rect.x + rect.width / 2,
            y: rect.y + rect.height / 2,
        };
    }
}
